<template>
    <v-sheet color="transparent" class="x-modules-list d-flex flex-wrap">
        <slot name="default"></slot>

        <div v-if="!$slots['default']">
            <x-short-btn v-for="(item, $index) in (options.items || options)" :key="$index"
                :label="$t(item.label)"
                :icon="item.icon"
                :image="item.image"
                :disabled="item.disabled"
                :hint="$t(item.hint)"
                :color="item.color"
                :size="size"
                :depressed="depressed"
                class="ma-1"
                @click="onClick(item, $event)">
            </x-short-btn>
        </div>

    </v-sheet>
</template>

<script>
export default {
    name: 'ModulesPanel',
    inheritAttrs: false,
    props: {
        options: {
            type: [Object,Array],
            default: () => {
                return {}
            }
        },
        size: {
            type: [String, Number],
            default: 80
        },
        depressed: {
            type: Boolean,
            default: true
        },
    },
    methods: {
        onClick(item, $event) {
            this.$emit('click', item, $event);

            if (item.click) {
                item.click(item, $event);
            }

            if (item.to) {
                if (typeof item.to === 'object') {
                    return this.$router.push(item.to);
                }
                if (item.to.indexOf('/') > -1) {
                    return this.$router.push({ path: item.to });
                }
                this.$router.push({ name: item.to });
            }
        },
    },
}
</script>

